	module.exports = {"data":{"markdownRemark":{"html":"<h1>Creative Commons Resources</h1>\n<p>Some people hate designing things, and that’s okay. But one thing that they might hate <em>more</em> is being sued for copyright infringement (for using content that someone else created without their expressed permission). </p>\n<p>Recently, I participated in a discussion on this topic and thought I’d share a handful of sites that I’ve turned to in the past for high quality, freely available content. If you are a designer, web developer, or just a blogger that needs some nice, shiny images, these will be right up your alley.</p>\n<p>All of the following resources should <a href=\"https://creativecommons.org/licenses/\">be licensed under Creative Commons</a> (or at equally-worded license) for you to use at your own leisure:</p>\n<ul>\n<li><a href=\"http://gratisography.com/\">Gratisography</a></li>\n<li><a href=\"https://unsplash.com/\">Unsplash</a></li>\n<li><a href=\"https://pixabay.com\">Pixabay</a></li>\n<li><a href=\"https://www.pexels.com\">Pexels</a></li>\n<li><a href=\"https://www.flickr.com/search/?advanced=1&#x26;license=2%252C3%252C4%252C5%252C6%252C9\">Flickr (Creative Commons License)</a></li>\n<li><a href=\"http://www.metmuseum.org/art/collection\">Metropolitan Museum Collection</a></li>\n<li><a href=\"http://images.google.com/hosted/life\">Life Magazine Archives</a></li>\n<li><a href=\"http://nos.twnsnd.co/\">New Old Stock</a></li>\n<li><a href=\"http://publicdomainarchive.com/\">Public Domain Archive</a></li>\n<li><a href=\"https://commons.wikimedia.org/wiki/Main_Page\">Wikimedia Creative Commons</a></li>\n</ul>\n<p>This is by no means an exhaustive list, but if you need some seriously high-resolution images to freely use - consider checking some of them out. </p>\n<blockquote>\n<p><strong>NOTE:</strong> If you are planning on using <em>any</em> of these images for non-personal or commercial use, please double-check all specifics for that particular image (as some sites vary).</p>\n</blockquote>","fields":{"slug":"/designer-tools/creative-commons-resources/"},"frontmatter":{"title":"Creative Commons Resources"}}},"pathContext":{"slug":"/designer-tools/creative-commons-resources/"}}

